.header{
    grid-area: h;
    background-color: #5A533C;
    padding: 10px;
    position: relative; /* 1 */

}
.header img {
    width: 70px;

}
.title {
    display: inline-block;
    color: #a46c34;
    font-size: 30px;
    font-weight: bold;
    position: absolute; /* 2 */
    top: 50%; /* 3 */
    transform: translate(0, -50%) /* 4 */
}

.loginBlock {
    float: right;
}
.loginBlock a {
    color: white;
}

.userAvatar{
    width: 60px;
    height: 60px;
    border-radius: 50%;
    object-fit: cover;
}